.dashboard-row {
  margin-bottom: 24px;
}
.dashboard-numberCol {
  margin-bottom: 24px;
}
.dashboard-numberCol .dashboard-numberCol-card .ant-card-body {
  display: flex;
  align-items: center;
}
.dashboard-numberCol .dashboard-numberCol-card .dashboard-numberCol-card-icon {
  float: left;
  width: 40px;
  margin-right: 20px;
}
.dashboard-numberCol .dashboard-numberCol-card .dashboard-numberCol-card-icon img {
  width: 40px;
  height: 40px;
}
.dashboard-numberCol .dashboard-numberCol-card .dashboard-numberCol-card-content {
  float: left;
}
.weather-wrap {
  padding: 20px;
  background: rgba(24, 144, 255, 0.6);
}
.weather-wrap .weather-head {
  display: flex;
  align-items: center;
}
.weather-wrap .weather-head .weather-head-left {
  flex: 1 1;
  font-weight: 500;
  font-size: 20px;
}
.weather-wrap .weather-head .weather-head-right p {
  margin-bottom: 0;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.65);
}
.weather-wrap .weather-head .weather-head-right p:first-child {
  font-weight: 500;
  font-size: 16px;
  color: #000;
}
.weather-wrap .weather-tips {
  color: rgba(0, 0, 0, 0.65);
}

